<template>
  <div>
    <Header style="z-index: 1000">
      <Menu mode="horizontal" :active-name="activeName">
        <div class="layout-logo">
        </div>
        <div class="layout-nav">
          <MenuItem name="home" to="/home">
            <Icon type="ios-home" size="24"></Icon>
            首页
          </MenuItem>
          <MenuItem name="finance" to="/finance">
            <Icon type="ios-stats" size="24"></Icon>
            我要出借
          </MenuItem>
          <MenuItem name="riskEducation" to="/riskEducation">
            <Icon type="ios-school" size="24"></Icon>
            网站课堂
          </MenuItem>
          <Submenu name="information">
            <template slot="title">
              <Icon type="ios-information-circle" size="24"></Icon>
              信息披露
            </template>
            <MenuItem name="organizationInfo" to="/information/baseInfo/organizationInfo">组织信息</MenuItem>
            <MenuItem name="complianceProgress" to="/information/complianceProgress">合规进度</MenuItem>
            <MenuItem name="external" to="/information/notice">对外公告</MenuItem>
            <MenuItem name="auditReport" to="/information/auditReport">审计报告</MenuItem>
            <MenuItem name="operationsReport" to="/information/baseInfo/operationsReport">运营报告</MenuItem>
            <MenuItem name="filingInfo" to="/information/baseInfo/filingInfo">备案信息</MenuItem>
            <MenuItem name="contactUs" to="/information/contactUs">联系我们</MenuItem>
          </Submenu>
          <MenuItem name="safe" to="/safe">
            <Icon type="ios-lock" size="24"></Icon>
            安全保障
          </MenuItem>
          <MenuItem name="center" to="/center">
            <Icon type="ios-person" size="24"></Icon>
            个人中心
          </MenuItem>
        </div>
      </Menu>
    </Header>
    <div class="headerBox"></div>
  </div>
</template>

<script>
export default {
  name: 'NavBar', // 模板名称
  components: {},
  data () {
    return {
      showLunarCalendar: true,
      throttleType: false,
      festival: [],
      throttleName: '',
      src: ''
    }
  },
  props: {
    'activeName': {
      type: String,
      required: true
    }
  },
  methods: {},
  created () {
    var self = this
    self.src = process.env.BASE_API
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .layout-logo {
    height: 80px;
    border-radius: 3px;
    float: left;
    position: relative;
    left: 20px;
  }
  .layout-nav{
    width: 780px;
    float: right;
  }
  .layout-footer-center{
    text-align: center;
  }
  .ivu-layout-header {
    width: 100%;
    height: 80px;
    padding: 0;
    position: fixed;
    line-height: 80px;
    z-index: 100;
    background: transparent;
  }
  .ivu-menu-horizontal {
    padding: 0 100px;
  }
  .ivu-menu-horizontal {
    min-width: 1425px;
    height: 80px;
    line-height: 80px;
  }
  .headerBox {
    height: 110px;
  }
</style>



